@font-face {
    font-family: 'Inter';
    src: url(./Inter-Black.woff2) format("woff2"),
         url(./Inter-Black.woff) format("woff"),
         url(./Inter-Black.ttf) format("ttf");
    font-style: normal;
    font-weight: 900;
    font-display: swap;
}

